body {
  background: #1e70bb;
  /* font-family: "Roboto", sans-serif; */
  color: white;
  font-family: "Raleway", sans-serif;
}

div.name-container {
  margin: 50px;
}

div.name-container h1 {
  width: 100%;
  text-align: center;
  font-weight: 500;
  font-size: 50px;
}

div.content-container {
  width: 50%;
  float: right;
  text-align: center;
}

div.content-container h3 {
  font-size: 40px;
  font-weight: 200;
  display: block;
}

div.content-container div.icon-links a i.fab {
  font-size: 50px;
  color: white;
  margin: 10px;
}

div.content-container div.icon-links a i.fab:hover {
  color: #1163ae;
}

